<template>
  <layout>
    <template v-slot:content>
      <main class="cart section container">
        <div
          v-if="cartItems.length > 0"
          class="cart__total-container">
          <p class="cart__total text-h4">Total price: {{ finalPrice.toLocaleString() }} cr.</p>
          <q-btn
            @click="formDialog = true"
            class="cart__total-btn"
            outline
            color="teal-5">Buy now</q-btn>
        </div>
        <q-banner
          v-if="unknownPricedItemsInCart"
          class="cart__banner">
          You have items in&nbsp;cart with&nbsp;fluctuating price. Our&nbsp;consultant will contact you&nbsp;after checkout.
        </q-banner>
        <q-btn
            to="/"
            outline
            class="cart__back-btn"
            color="secondary">Go back to store</q-btn>
        <q-list
          v-if="cartItems.length > 0"
          dark>
          <q-card
            class="bg-grey-7 cart__item"
            dark
            bordered
            v-for="(starship, index) in cartItems"
            :key="`${starship.id}-${index}`">
            <q-card-section>
              <p class="cart__item-title">{{ starship.name }}</p>
            </q-card-section>
            <q-card-actions>
              <q-btn
                @click="handleRemoveFromCart(starship)"
                class="cart__item-btn"
                outline
                color="secondary">Remove from cart</q-btn>
            </q-card-actions>
          </q-card>
        </q-list>
        <p class="text-h5" v-if="cartItems.length <= 0 && !fetching">No items in cart yet</p>
        <q-dialog v-model="formDialog">
          <q-card dark class="bg-grey-7">
            <q-card-section>
              <cart-form></cart-form>
            </q-card-section>
          </q-card>
        </q-dialog>
      </main>
    </template>


  </layout>
</template>

<script>
  import Layout from '@/app/common/layout/Layout';
  import { mapGetters, mapState, mapActions } from 'vuex';
  import CartForm from './CartForm';

  export default {
    name: 'CartMain',
    components: {
      Layout,
      CartForm,
    },
    data() {
      return {
        formDialog: false,
      };
    },
    computed: {
      ...mapState(['fetching']),
      ...mapGetters(['cartItems', 'finalPrice', 'unknownPricedItemsInCart']),
    },
    methods: {
      ...mapActions(['updateStarshipValue', 'removeFromCart']),
      async handleRemoveFromCart(starship) {
        await this.updateStarshipValue({ starship, inCart: false });
        await this.removeFromCart(starship);
        return null;
      }
    },
  };
</script>

<style lang="scss" scoped>
  .cart__total-container {
    display: flex;
    justify-content: space-between;
    margin-bottom: 1.25rem;
  }

  .cart__item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 1.25rem;
  }

  .cart__item-title {
    margin-bottom: 0;
    font-size: 1.25rem;
  }

  .cart__item-btn {
    margin-right: 1.25rem;
  }

  .cart__back-btn {
    margin-bottom: 1.25rem;
    -webkit-appearance: none;
  }

  .cart__banner {
    margin-bottom: 1.5rem;
    color: var(--white);
    background-color: transparent;
    border: 1px solid var(--yellow);
  }
</style>
