<template>
  <q-form
    @submit="handleSubmit"
    @reset="clearForm">
    <q-input
      class="cart__form-input"
      v-model="name"
      label="Your name *"
      lazy-rules
      :rules="[val => val && val.length > 0 || 'Please type something']"/>

    <q-input
      class="cart__form-input"
      v-model="race"
      label="Your race *"
      lazy-rules
      :rules="[val => val && val.length > 0 || 'Please type something']"/>

    <q-date
      class="cart__form-input"
      v-model="date"
      dark
      today-btn/>

    <div>
      <q-btn
        outline
        label="Submit"
        type="submit"
        color="secondary"/>
      <q-btn
        label="Reset"
        type="reset"
        color="secondary"
        flat />
    </div>
  </q-form>
</template>

<script>
import { mapActions } from 'vuex';
  export default {
    name: 'CartForm',
    data() {
      return {
        name: '',
        race: '',
        date: '',
      };
    },
    methods: {
      ...mapActions(['clearCart', 'updateStarshipsValue']),
      async handleSubmit() {
        await this.clearCart();
        await this.updateStarshipsValue();
        await this.$router.push('/');
        this.$q.notify({
          color: 'teal-5',
          textColor: 'white',
          message: 'We accepted your order. Our consultant will contact you shortly.'
        });
        return null;
      },
      clearForm() {
        this.name = '';
        this.race = '';
        this.date = '';
        return null;
      },
    },
  };
</script>

<style lang="scss" scoped>
  .cart__form-input {
    margin-bottom: 1.25rem;
  }
</style>
